<template>
  <div class="main-container">
    <!-- <button type="button" @click="handleStart">开始随机</button>
    <button type="button" @click="handleStop">停止</button> -->
    <!-- <div class="pic-box">
      <div class="background"><img v-if="this.randomNum[0]" :src="require(`@/assets/images/background-${this.randomNum[0]}.png`)" alt="" /></div>
    </div> -->
    <div class="pic-wrap">
      <div class="titile">SSR</div>
      <div class="flex-list">
        <div class="content" v-for="(item, index) in totalNumArr.map((r) => (getPool(r, 'ssr') ? r.toString().split('') : undefined)).filter((s) => s)" :key="index + 'ssr'">
          <div class="bg">
            <img :src="require(`@/assets/images/bg-${item[0]}.png`)" alt="" />
            <div class="body"><img :src="require(`@/assets/images/body-${item[1]}.png`)" alt="" /></div>
            <div class="head"><img :src="require(`@/assets/images/head-${item[2]}.png`)" alt="" /></div>
            <div class="foot"><img :src="require(`@/assets/images/foot-${item[3]}.png`)" alt="" /></div>
            <div class="icon"><img :src="require(`@/assets/images/icon-${item[4]}.png`)" alt="" /></div>
          </div>
        </div>
      </div>
      <div class="titile">SR</div>
      <div class="flex-list">
        <div class="content" v-for="(item, index) in totalNumArr.map((r) => (getPool(r, 'sr') ? r.toString().split('') : undefined)).filter((s) => s)" :key="index + 'sr'">
          <div class="bg">
            <img :src="require(`@/assets/images/bg-${item[0]}.png`)" alt="" />
            <div class="body"><img :src="require(`@/assets/images/body-${item[1]}.png`)" alt="" /></div>
            <div class="head"><img :src="require(`@/assets/images/head-${item[2]}.png`)" alt="" /></div>
            <div class="foot"><img :src="require(`@/assets/images/foot-${item[3]}.png`)" alt="" /></div>
            <div class="icon"><img :src="require(`@/assets/images/icon-${item[4]}.png`)" alt="" /></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'homeIndex',
  data() {
    return {
      timer: null,
      num: [],
      randomNum: [],
      totalNumArr: [],
    };
  },
  mounted() {
    this.totalNumFunc();
  },
  methods: {
    handleStart() {
      this.timer = setInterval(this.randomFunc, 100);
    },
    randomFunc() {
      this.randomNum = [];
      for (let i = 0; i < 5; i++) {
        this.randomNum.push(Math.floor(Math.random() * 5 + 1));
      }
      console.log(this.randomNum, 'num');
    },
    totalNumFunc() {
      while (this.totalNumArr.length < 3125) {
        this.num = [];
        for (let i = 0; i < 5; i++) {
          this.num.push(Math.floor(Math.random() * 5 + 1));
        }
        let resultNum = parseInt(this.num.join(''));
        this.totalNumArr.includes(resultNum) ? false : this.totalNumArr.push(resultNum);
        // this.totalNumArr.filter((item, index, arr) => arr.indexOf(item) === index);
      }
    },
    //卡池
    getPool(item, type) {
      let itemArr = item.toString().split('');
      let result = false;
      switch (type) {
        case 'ssr':
          result = itemArr.every((i) => [4, 5].includes(parseInt(i)));
          break;
        case 'sr':
          result = !itemArr.every((i) => [4, 5].includes(parseInt(i)));
      }

      return result;
    },
    handleStop() {
      clearInterval(this.timer);
    },
  },
};
</script>
